import styled from "styled-components";
import { React, useState } from "react";
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import Zoom from "react-reveal/Zoom";
import Fade from 'react-reveal/Fade';

// Images //

import herramientas from "../img/Herramientas.svg"
import SkillsProgress from "../img/SkillsProgress.svg"

// Images //


function Habilidades() {
  const [isSkillToggled, setSkillToggled] = useState(false);

  const ToggleSwitch = () => {
    isSkillToggled ? setSkillToggled(false) : setSkillToggled(true);
  };

  return (
    <Fade left cascade>
      <Container id="habilidades">
        <Header>
          <h1 >
            <b>//</b> Habilidades
          </h1>
          <div></div>
        </Header>
        <ContenedorInformacion>
          <Textos>
            <div>
              <h1>• Diseño UI/UX/Branding</h1>
              <p>
                He creado distintos diseños para creadores de contenido de
                plataformas de livestreaming ayudándoles a rediseñar su marca y
                logrando un mejor alcance a los usuarios o una vista más
                profesional. Además de crear interfaces para aplicaciones web y
                aplicaciones móviles con diseños fáciles de usar, enfocándome en
                Material Design, Apple Design o un diseño único para cada
                aplicación.
              </p>
            </div>
            <div>
              <h1>• Desarrollo Web</h1>
              <p>
                Puedo crear exitosamente sitios web responsivos que son rápidos,
                fáciles de usar basándome en la experiencia del usuario y con
                código perfectamente entendible para futuros programadores
                Front-End. He trabajado en distintos proyectos personales
                creados por mi, participado en otros proyectos de una empresa
                familiar dedicada al desarrollo de software y estoy trabajando
                en distintos sitios web para uso propio.
              </p>
            </div>
          </Textos>
        <Zoom>
        <TarjetaHabilidades>
            <img
              src={
                isSkillToggled
                  ? herramientas
                  : SkillsProgress
              }
              className={isSkillToggled ? "tools" : "none"}
            />
            <div>
              <button
                onClick={ToggleSwitch}
                className={isSkillToggled ? "btn2" : "btn1"}
              ></button>
              <button
                onClick={ToggleSwitch}
                className={isSkillToggled ? "btn1" : "btn2"}
              ></button>
            </div>
          </TarjetaHabilidades>
        </Zoom>
        </ContenedorInformacion>
      </Container>
    </Fade>
  );
}

export default Habilidades;

const Container = styled.div`
  display: flex;
  margin-left: 110px;
  height: 100vh;
  flex-direction: column;
  background-color: ;

  @media (max-width: 1200px){
    margin-left: 0px;
    height: auto;
  }
`;

const Header = styled.div`
  padding-left: 50px;
  font-size: 23px;
  color: white;
  height: 150px;

  @media (max-width: 1200px){
    padding-left: 0px;
    padding-top: 60px;
    font-size: 22px;
    text-align: center;
  }

  b {
    background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  }

  div {
    background-color: #fff;
    width: 85vw;
    height: 3px;
    @media (max-width: 1200px){
      width: 0vh;
    }
  }
`;

const ContenedorInformacion = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  @media (max-width: 1200px){
    flex-direction: column;
    height: auto;
  }
`;

const Textos = styled.div`
  color: white;
  width: 30em;
  font-size: 22px;
  padding: 0px 0px;
  font-weight: 100;

  @media (max-width: 1200px){
    font-size: 18px;
    width: 80%;
  }

  div {
    h1 {
      background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
      position: relative;
      font-family: "Gilroy ExtraBold";
      font-size: 32px;

      @media (max-width: 1200px){
        font-size: 25px;
      }

      &::after {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        position: absolute;
        left: -50px;
        top: 23px;
        border-radius: 50%;
      }
    }
  }
`;

const TarjetaHabilidades = styled.div`
  background-color: white;
  width: 600px;
  height: 550px;
  border-radius: 40px;
  position: relative;
  transition: all 0.3s ease-in-out;

  @media (max-width: 1200px){
    width: 100%;
    border-radius: 0px;
    height: 20%;
    display: flex;
    justify-content: center;

  }

  img {
    padding: 70px;
    width: 450px;
    transition: all 0.3s ease-in-out;

    @media (max-width: 1200px){
      width: 80%;
    }
  }

  .tools {
    width: 425px;
    padding: 80px;

    @media (max-width: 1200px){
      width: 60%;
    }
  }

  div {
    position: absolute;
    bottom: 2em;
    left: 15em;
    @media (max-width: 1200px){
      flex-direction: row;
      display: flex;
      left: 35%;
      bottom: 30px;
    }
  }

  .btn1 {
    width: 20px;
    height: 20px;
    margin: 0 20px 0;
    border: none;
    border-radius: 50%;
    background-color: #f2f2f2;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    @media (max-width: 1200px){

    }
  }

  .btn2 {
    width: 20px;
    height: 20px;
    margin: 0 20px 0;
    border: none;
    border-radius: 50%;
    background-color: grey;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
  }
`;
