import {React, useState} from "react";
import styled from "styled-components";
import { useForm } from "react-hook-form";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebook,
  faInstagram,
  faLinkedin,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import Zoom from "react-reveal/Zoom";
import Fade from 'react-reveal/Fade';

// Images //

import blob from "../img/blob.png"

// Images //

function Contacto() {


  const facebook = () => {
    window.open("https://www.facebook.com/Lizcuack-104751278004796/", "_blank");
  };

  const instagram = () => {
    window.open("https://www.instagram.com/lizcuack/", "_blank");
  };

  const twitter = () => {
    window.open("https://twitter.com/lizcuack", "_blank");
  };

  const linkedin = () => {
    window.open("https://www.linkedin.com/in/landreshdz/", "_blank");
  };

  return (
    <Container>
      <Fade top>
      <Header>
        <h1>
          <b>//</b> Contacto
        </h1>
      </Header>
      </Fade>
      <ContenedorInformacion>
        <Fade left cascade>
        <div className="TextosContacto">
          <h1 className="Contacto_textos">
            Estoy interesado en cualquier oportunidad de trabajo sin importar la
            magnitud del proyecto.
          </h1>
          <h1 className="Contacto_textos">
            Se libre de llenar el formulario o contactarme por alguna de mis
            redes sociales.
          </h1>
        </div>
        </Fade>
        <Fade right cascade>
        <div className="Formulario__tarjeta">
          <h1>¡Escríbeme!</h1>
          <form >
            <input type="text" placeholder="Nombre" name="nombre" />
            <input type="text" placeholder="Correo" name="email" />
            <input type="text" placeholder="Asunto" name="Asunto" />
            <textarea
              name="Mensaje"
              placeholder="Mensaje"
              rows="8"
              cols="80"
            ></textarea>
            <input type="submit" placeholder="Enviar" name="Enviar"></input>
          </form>
          <div className="redes">
            <FontAwesomeIcon
              className="header__iconos"
              onClick={facebook}
              icon={faFacebook}
            ></FontAwesomeIcon>
            <FontAwesomeIcon
              className="header__iconos"
              onClick={instagram}
              icon={faInstagram}
            ></FontAwesomeIcon>
            <FontAwesomeIcon
              className="header__iconos"
              onClick={linkedin}
              icon={faLinkedin}
            ></FontAwesomeIcon>
            <FontAwesomeIcon
              className="header__iconos"
              onClick={twitter}
              icon={faTwitter}
            ></FontAwesomeIcon>
          </div>
        </div>
        </Fade>
      </ContenedorInformacion>
    </Container>
  );
}

export default Contacto;

const Container = styled.div`
  display: flex;
  margin-left: 110px;
  height: 100vh;
  flex-direction: column;
  background: url(${blob});
  background-position: 90%;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;

  @media (max-width: 1200px) {
    margin-left: 0px;
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    justify-content: center;
    align-items: center;
    position: static;

  }
`;

const Header = styled.div`
  padding-left: 50px;
  font-size: 24px;
  color: white;
  font-family: "Gilroy ExtraBold";

  @media (max-width: 1200px){
    padding-left: 0px;
    text-align: center;
    padding-top: 70px;
    font-size: 24px;
    }

  b {
    background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  }

  div {
    background-color: #fff;
    width: 85vw;
    height: 3px;

    @media (max-width: 1200px){
      width: 0px;
    }
  }
`;

const ContenedorInformacion = styled.div`
  display: flex;
  flex-direction: row;
  height: 70%;
  justify-content: flex-start;
  align-items: center;
  color: white;
  padding-left: 30px;

  @media (max-width: 1200px){
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding-left: 0px;
    }



  .TextosContacto {
    width: 700px;

    @media (max-width: 1200px){
     width: 300px; 
    }
    h1 {
      font-weight: 200;
      font-size: 24px;

      @media (max-width: 1200px){
        font-size: 22px;
    }
    }

    .Contacto_textos {
      padding: 30px;

      @media (max-width: 1200px){
      width: 90%;
      text-align: center;
      padding: 15px;
      margin: auto;
      font-weight: 400;
      }
    }
    }
  }

  .Formulario__tarjeta {
    width: 700px;
    height: 100vh;
    backdrop-filter: blur(24px) saturate(180%);
    -webkit-backdrop-filter: blur(24px) saturate(180%);
    background-color: rgba(255, 255, 255, 0.05);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 10%;
    top: 0;

    @media (max-width: 1200px){
      width: 100%;
      height: auto;
      position: static;
    }

    .redes {
      font-size: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .header__iconos {
      padding: 20px 20px;
      transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);

      &:hover {
        color: #f5d10d;
        transform: scale(1.4);
      }
    }

    h1 {
      text-align: center;
    }
  }

  form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    align-items: center;

    input {
      background-color: rgba(255, 255, 255, 0.3);

      @media (max-width: 1200px){
      margin: 20px;
    }

      &::placeholder {
        color: white;
        font-weight: 500;
        font-size: 16px;
      }
    }

    input[type="submit"] {
      width: 150px;
      height: 42px;
      border: none;
      background: white;
      font-weight: 700;
      font-size: 18px;
      cursor: pointer;
      transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);

      @media (max-width: 1200px){
        grid-row: 4/5;
        grid-column: 1/4;
        align-self: center;
      
    }

      &:hover {
        background-color: #fff;
      }
    }

    input:nth-child(1) {
      grid-column: 1 / 3;
      height: 50px;
      border: none;
      padding: 0 20px 0px;
      color: white;
      margin-right: 30px;
      align-self: flex-end;
    }

    input:nth-child(2) {
      grid-column: 3 / 5;
      height: 50px;
      border: none;
      padding: 0 20px 0px;
      margin-left: 30px;
      align-self: flex-end;
    }

    input:nth-child(3) {
      grid-column: 1 / 5;
      grid-row: 2/3;
      height: 50px;
      border: none;
      padding: 0 20px 0px;
    }

    textarea {
      grid-column: 1/ 5;
      background-color: rgba(255, 255, 255, 0.3);
      color: white;
      max-width: 607px;
      height: 150px;
      max-height: 150px;
      min-width: 607px;
      min-height: 150px;
      border: none;
      font-size: 18px;

      @media (max-width: 1200px){
      min-width: 22em;
      max-width: 22em;
      min-height: 150px;
      max-height: 150px;
      grid-column: 2/4;
      align-items: center;
    }

      &:focus {
      }
    }
  }
`;
