import { React, useState, useEffect } from "react";
import styled from "styled-components";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import logopersonal from "../img/logopersonal.svg"
import navbar from "../img/Navbar.svg"

function NavbarMobile() {

    const [switchToggled, setSwitchToggled] = useState(false);

    const ToggleSwitch = () => {
        switchToggled ? setSwitchToggled(false) : setSwitchToggled(true);
        console.log(switchToggled)
    }

  return (
    <Navegacion>
      <Nav>
        <img src={logopersonal}></img>
        <img src={navbar} onClick={ToggleSwitch}></img>
      </Nav>
      <Links className={switchToggled ? "visible" : "invisible"}>
        <Link to="/" className="Enlaces" onClick={ToggleSwitch}>
          <span>Inicio</span>
        </Link>
        <Link to="/habilidades" className="Enlaces" onClick={ToggleSwitch}>
          <span>Habilidades</span>
        </Link>
        <Link to="/proyectos" className="Enlaces" onClick={ToggleSwitch}>
          <span>Proyectos</span>
        </Link>
        <Link to="/blog" className="Enlaces" onClick={ToggleSwitch}>
          <span>Blog</span>
        </Link>
        <Link to="/Contacto" className="Enlaces" onClick={ToggleSwitch}>
          <span>Contacto</span>
        </Link>
      </Links>
    </Navegacion>
  );
}

export default NavbarMobile;


const Navegacion = styled.div`


z-index: 200;
position: fixed;

    .visible{
        display: flex;
        animation: swipeaboutme 0.7s cubic-bezier(0.445, 0.05, 0.55, 0.95) backwards;
    }

    .invisible{
        display:none;        

    }

`

const Nav = styled.nav`
  background-color: #0c0c0c;
  width: 100vw;
  height: 70px;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;


  . span {
    padding: 20px;
    color: white;
  }

  img {
    width: 60px;
    color: black;
    height: 60px;
    padding-left: 40px;
    cursor: pointer;
  }
`;

const Links = styled.nav`
  position: fixed;
  z-index: 200;
  width: 100vw;
  height: 100vh;
  background-color: #8B5CF6;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-decoration: none;

  .visible{
        opacity: 100;
        display: flex;
        animation: swipeaboutme 0.7s cubic-bezier(0.445, 0.05, 0.55, 0.95) backwards;
    }

    .invisible{
        opacity: 0;
        display: none;
        

    }

  .Enlaces {
    text-decoration: none;
    padding: 30px;
    position: relative;
  }

  span {
    font-size: 32px;
    font-weight: bold;
    color: black;
    text-decoration: none;
  }
`;
