import {React, useState} from 'react';
import styled from 'styled-components'
import { BrowserRouter as Router,
    Switch,
    Route, 
    Link} from "react-router-dom"
import user from "../img/user.svg"
import skills from "../img/skills.svg"
import folder from "../img/folder.svg"
import blog from "../img/blog.svg"
import send from "../img/send.svg"
import logo from "../img/logoamarillo.png"


function Navegacion() {

    
    return ( 
        <Nav>
            <div>
            <Link to='/'>
                <img src={user} />
           </Link>
           <Link to='/habilidades'>
                <img src={skills}/>
           </Link>
           <Link to='/proyectos'>
                <img src={folder}/>
           </Link>
           <Link to='/blog'>
                <img src={blog}/>
           </Link>
           <Link to="/Contacto">
                <img src={send}/>
           </Link>
            </div>
            <div>
                <img className='logoamarillo' src={logo}/>
            </div>

        </Nav>
     );
}

export default Navegacion;

const Nav = styled.nav`
    width: 110px;
    height: 100vh;
    background-color: #0C0C0C;
    display: flex;
    flex-direction: column;
    position:fixed;
    z-index: 10;

    div{
        .logoamarillo {
            width: 110px;
            position: absolute;
            bottom: 0;
            left:0;
        }
    }

    a{
        display: flex;
        height: 115px;
        align-items: center;
        justify-content: center;
        transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
        text-decoration: none;
        color: black;
        font-family: "Gilroy ExtraBold";
        
        &:nth-child(1)::after{
            content: "";
            display: block;
            font-size: 20px;
            font-weight: bold;
            text-align: center;
            width: 0px;
            height: 0px;
            line-height: 43px;
            background-color: #FFF;
            position: absolute;
            left: 110px;
            transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);
        }

        &:nth-child(2)::after{
            content: "";
            display: block;
            font-size: 20px;
            text-align: center;
            width: 0px;
            height: 0px;
            background-color: #fff;
            position: absolute;
            left: 110px;
            font-weight: bold;
            line-height: 43px;
            transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);

        }

        &:nth-child(3)::after{
            content: "";
            display: block;
            font-size: 20px;
            text-align: center;
            width: 0px;
            height: 0px;
            background-color: #fff;
            position: absolute;
            left: 110px;
            font-weight: bold;
            line-height: 43px;
            transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);

        }

        &:nth-child(4)::after{
            content: "";
            display: block;
            font-size: 20px;
            text-align: center;
            line-height: 43px;
            width: 0px;
            height: 0px;
            background-color: #fff;
            position: absolute;
            left: 110px;
            font-weight: bold;
            transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);

        }

        &:nth-child(5)::after{
            content: "";
            display: block;
            font-size: 20px;
            text-align: center;
            width: 0px;
            height: 0px;
            background-color: #fff;
            position: absolute;
            left: 110px;
            font-weight: bold;
            line-height: 43px;
            transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);

        }

        /* Hover */
        
        &:nth-child(1):hover::after{
            content: "INICIO";
            width: 100px;
            height: 40px;
        }

        &:nth-child(2):hover::after{
            content: "HABILIDADES";
            width: 160px;
            height: 40px;
        }

        &:nth-child(3):hover::after{
            content: "PROYECTOS";
            width: 145px;
            height: 40px;
        }

        &:nth-child(4):hover::after{
            content: "BLOG";
            width: 100px;
            height: 40px;
        }

        &:nth-child(5):hover::after{
            content: "CONTACTO";
            width: 145px;
            height: 40px;
        }

        &:hover{
        background-color: rgba(221,221,221,0.2);
        }
        
        img{
            width: 35px;
        }
    }



    }
`

const Img = styled.img`
    color: white;
`