import { React, useState } from "react";
import styled from "styled-components";
import Zoom from "react-reveal/Zoom";
import Fade from "react-reveal/Fade";
import Slide from "react-reveal/Slide";

// Images //

import arbolabport from "../img/arbolabport.png"
import stport from "../img/stport.png"

// Images //

function Proyectos() {
  return (
    <Fade right cascade>
      <Container>
        <Header>
          <h1>
            <b>//</b> Proyectos
          </h1>
          <div></div>
        </Header>

        <ContenedorInformacion>
          <EstiloProyecto>
            <LogoProyecto>
              <img src={arbolabport}></img>
            </LogoProyecto>
            <div>
              <h1>Proyecto: Arbolab</h1>
              <h2>
                Es un proyecto de una empresa familiar en la cual se desarrolló
                todo lo relacionado a branding junto con un diseño de página web
                y un diseño de su interfaz móvil.
              </h2>
              <button>Conocer más</button>
            </div>
          </EstiloProyecto>
          <EstiloProyecto>
            <LogoProyecto>
              <img src={stport}></img>
            </LogoProyecto>
            <div>
              <h1>Proyecto: StreamerThings</h1>
              <h2>
                Es un proyecto personal dedicado al contenido didactico para
                creadores de contenido en redes sociales. Se diseñó y programó
                la interfaz de la web y de las distintas aplicaciones que
                proporciona.
              </h2>
              <button>Conocer más</button>
            </div>
          </EstiloProyecto>
        </ContenedorInformacion>
      </Container>
    </Fade>
  );
}

export default Proyectos;

const Container = styled.div`
  display: flex;
  margin-left: 110px;
  flex-direction: column;

  @media (max-width: 1200px) {
    margin-left: 0px;
    height: auto;
  }
`;

const Header = styled.div`
  padding-left: 50px;
  font-size: 23px;
  color: white;

  @media (max-width: 1200px) {
    padding-left: 0px;
    padding-top: 60px;
    font-size: 22px;
    text-align: center;
  }

  b {
    background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  }

  div {
    background-color: #fff;
    width: 85vw;
    height: 3px;

    @media (max-width: 1200px) {
      width: 0vh;
    }
  }
`;

const ContenedorInformacion = styled.div`
  display: flex;
  height: 80%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
`;

const EstiloProyecto = styled.div`
  display: flex;
  padding: 30px 50px 0;

  @media (max-width: 1100px) {
    flex-direction: column;
    padding: 0px 0px 0;
  }

  div {
    padding: 0 60px 0;

    @media (max-width: 1200px) {
      padding: 0px;
      text-align: center;
    }
  }

  img {
    width: 310px;
    height: 310px;
    @media (max-width: 1100px) {
    }
  }

  h1 {
    background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    margin-top: 60px;
    font-size: 32px;
  }

  h2 {
    padding: 0px;
    font-weight: 200;
  }

  button {
    width: 160px;
    height: 50px;
    border: none;
    background: linear-gradient(145deg,#8B5CF6, #EC4899);
    font-size: 18px;
    cursor: pointer;
    font-weight: 700;
  }
`;

const LogoProyecto = styled.div`
  width: 400px;
  height: 400px;
  position: relative;

  @media (max-width: 1100px) {
    width: 100%;
    height: 300px;
    margin: 40px auto;
  }
`;
