import { React, useState } from "react";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebook,
  faInstagram,
  faLinkedin,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import { faAngleLeft } from "@fortawesome/free-solid-svg-icons";
import Zoom from "react-reveal/Zoom";
import Fade from 'react-reveal/Fade';
import profilepic from "../img/header__foto.png"

function Header() {
  const [switchToggled, setSwitchToggled] = useState(false);

  const ToggleSwitch = () => {
    switchToggled ? setSwitchToggled(false) : setSwitchToggled(true);
    console.log(switchToggled);
  };

  const facebook = () => {
    window.open("https://www.facebook.com/Lizcuack-104751278004796/", "_blank");
  };

  const instagram = () => {
    window.open("https://www.instagram.com/lizcuack/", "_blank");
  };

  const twitter = () => {
    window.open("https://twitter.com/lizcuack", "_blank");
  };

  const linkedin = () => {
    window.open("https://www.linkedin.com/in/landreshdz/", "_blank");
  };

  // Media Querys

  return (
    <Container id="header">
      <Fade left>
        <Textos>
          <h1>
            Hola,
            <br />
            soy <span>Alex.</span>
          </h1>
          <h2>
            <span>Desarrollador</span> Front End - <span>Diseñador</span> UI/UX{" "}
          </h2>
          <button> Contactame </button>
        </Textos>
      </Fade>

      <Zoom>
        <Foto>
          <img src={profilepic} />
        </Foto>
      </Zoom>

      <Fade right>
        <Redes>
          <FontAwesomeIcon
            className="header__iconos"
            onClick={facebook}
            icon={faFacebook}
          ></FontAwesomeIcon>
          <FontAwesomeIcon
            className="header__iconos"
            onClick={instagram}
            icon={faInstagram}
          ></FontAwesomeIcon>
          <FontAwesomeIcon
            className="header__iconos"
            onClick={linkedin}
            icon={faLinkedin}
          ></FontAwesomeIcon>
          <FontAwesomeIcon
            className="header__iconos"
            onClick={twitter}
            icon={faTwitter}
          ></FontAwesomeIcon>
          <button
            id="header__aboutme__trigger"
            className={
              switchToggled
                ? "header__aboutme__trigger_on"
                : "header__aboutme__trigger__off"
            }
            onClick={ToggleSwitch}
          >
            <FontAwesomeIcon icon={faAngleLeft}></FontAwesomeIcon>
          </button>
        </Redes>
      </Fade>

      <AcercaDe
        id="header__aboutme"
        className={switchToggled ? "visible" : "invisible"}
      >
        <TextosAcercaDe>
          <h1>Acerca de mi</h1>
          <p>
            Hola, soy Luis Alexis Andrés Hernández. Vivo actualmente en México
            siendo el Español mi idioma nativo, con conocimiento intermedio
            sobre el inglés.
            <br></br>
            <br></br>
            Soy un desarrollador Front-End enfocado principalmente a frameworks
            como React y Angular, dominando librerías como Boostrap5 y Tailwind
            CSS.
            <br></br>
            <br></br>
            También soy diseñador UI/UX con amplios conocimientos en Figma y la
            suite de Adobe ya que utilizo After Effects para realizar
            animaciones básicas y Premiere o Davinci Resolve para ediciones de
            video no tan complejas.
            <br></br>
            <br></br>
            Empecé mi carrera como freelancer hace relativamente poco con el fin
            de expandir mis conocimientos hacia el desarrollo de aplicaciones.
          </p>
        </TextosAcercaDe>
      </AcercaDe>
    </Container>
  );
}

export default Header;

const Container = styled.div`
  display: flex;
  margin-left: 110px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;


  @media (max-width: 1200px) {
    flex-direction: column;
    margin-left: 0px;
    height: auto;
  }

  .visible {
    opacity: 100;
    animation: swipeaboutme 0.7s cubic-bezier(0.445, 0.05, 0.55, 0.95) backwards;
  }

  .invisible {
    opacity: 0;
  }

  .header__aboutme__trigger_on {
    transform: rotate(180deg);
    background-color: #fff;
    right: 900px;

    @media (max-width: 1200px){
        right: 40%;
    }
  }
`;

const Textos = styled.div`
  display: flex;
  flex-direction: column;
  color: white;
  padding: 150px 50px;

  @media (max-width: 1200px) {
    text-align: center;
    padding: 150px 50px;
  }

  span {
  background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  }

  h1 {
    margin-top: -50px;
    font-size: 72px;
    line-height: 120px;

    @media (max-width: 1200px) {
      font-size: 48px;
      line-height: 80px;
    }
  }

  h2 {
    margin-top: -60px;
    font-size: 32px;
    position: relative;
    font-weight: 500;

    @media (max-width: 1200px) {
      font-size: 24px;
      margin-top: -20px;

      &::after {
        content: none;
        display: none;
      }
    }

    &::after {
      content: "";
      display: block;
      background-color: #EC4899;
      width: 60px;
      height: 15px;
      position: absolute;
      top: 20px;
      left: -70px;
    }
  }

  button {
    width: 230px;
    height: 58px;
    font-size: 27px;
    color: white;
    background-color: #090909;
    border: none;
    border: 2px solid #EC4899;
    transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    cursor: pointer;

    @media (max-width: 1200px) {
      width: 150px;
      height: 40px;
      font-size: 18px;
      margin: auto;
    }

    &:hover {
      background: linear-gradient(270deg,#8B5CF6, #EC4899);
      color: black;
      border: none;
    }
  }
`;

const Foto = styled.div`
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 1200px) {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  img {
    width: 95%;

    @media (max-width: 1200px) {
      width: 70%;
    }
  }
`;

const Redes = styled.div`
  display: flex;
  height: 95vh;
  flex-direction: column;
  padding: 10px;
  color: white;
  align-items: center;

  @media (max-width: 1200px) {
      flex-direction: row;
      height: 5vh;
  }

  .header__iconos {
    padding: 15px;
    font-size: 30px;
    transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    border-radius: 50%;
    

    &:hover {
      background: linear-gradient(270deg,#8B5CF6, #EC4899);
      transform: scale(1.4);
      border-radius: 50%;
    }
  }

  button {
    position: absolute;
    right: 0;
    top: 50%;
    width: 70px;
    height: 70px;
    background: linear-gradient(0deg,#8B5CF6, #EC4899);
    border: none;
    cursor: pointer;
    font-size: 30px;
    transition: all 0.7s ease-in-out;
  }
`;

const AcercaDe = styled.div`
  width: 800px;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 100px;
  backdrop-filter: blur(16px) saturate(148%);
  -webkit-backdrop-filter: blur(11px) saturate(148%);
  background-color: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  color: white;
  font-weight: 400;
  text-align: left;
  opacity: 100;
  transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);

  @media (max-width: 1200px) {
      width: 100%;;
      height: auto;
      right: 0;
      top: 60%;
  }

  h1 {
    background: -webkit-linear-gradient(145deg,#8B5CF6, #EC4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    text-align: center;

    @media (max-width: 1200px){
        font-size: 28px;
    }
  }
`;
const TextosAcercaDe = styled.div`
  padding: 70px;
  font-size: 24px;
  font-weight: 300;

  @media (max-width: 1200px){
      font-size: 14px;
  }
`;
